* {
  box-sizing: border-box;
  font-family: $font;
}

html {
  font-size: 1vw;
}

body,
html {
  background-color: $back-grey;
}

#app {
  box-shadow: 0 4px 26px 0 #e1e1e1;
  background-color: $white;
  margin-left: auto;
  margin-right: vw(170);
  margin-top: vw(114);
  width: vw(1300);
  border-radius: 8px;
  margin-bottom: vw(43);
  overflow: hidden;

  @media (max-width: 1366px) {
	margin-right: auto;
	margin-top: vwM(45);
	width: vwM(1300);
	border-radius: 8px;
	margin-bottom: vwM(45);
  }

  @media (max-width: 640px){
	margin-top: vwS(60);
	margin-left: 0;
	margin-right: 0;
	width: 100%;
	max-width: 100%;
	border-radius: 0;
  }
}

header {
  width: 100%;
  padding-top: vw(30);
  padding-left: vw(45);
  padding-right: vw(45);

  @media (max-width: 1366px) {
	padding-top: vwM(30);
	padding-left: vwM(45);
	padding-right: vwM(45);

  }

  @media (max-width: 640px){
	padding-top:  vwS(30);
	padding-left: vwS(15);
	padding-right: vwS(15);
  }
}

nav {
  width: 100%;
  display: flex;
  align-items: center;

  .logo {
	font-size: rem(14);
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.43;
	letter-spacing: vw(1.5);
	color: $logo-color;
	text-decoration: none;

	@media (max-width: 1366px) {
	  font-size: remM(14);
	  letter-spacing: vwM(1.5);
	}

	@media (max-width: 640px){
	  font-size: remS(14);
	  align-items: flex-start;
	}
  }

  .user {
	display: inline-flex;
	align-items: center;
	text-decoration: none;
	margin-left: vw(15);

	@media (max-width: 1366px) {
	  margin-left: vwM(15);
	}

	@media (max-width: 640px){
	  font-size: vwS(14);
	  margin-left: vwS(15);
	  align-items: flex-start;
	}

	&-time {
	  font-size: rem(14);
	  font-weight: normal;
	  font-stretch: normal;
	  font-style: normal;
	  line-height: 1.36;
	  letter-spacing: normal;
	  color: $logo-color;
	  margin-right: vw(15);

	  @media (max-width: 1366px) {
		font-size: remM(14);
		margin-right: vwM(15);
	  }

	  @media (max-width: 640px){
		font-size: remS(14);
		margin-right: vwS(15);
		margin-top: vwS(-3);
	  }
	}

	img {
	  width: vw(15);
	  margin-right: vw(8);

	  @media (max-width: 1366px) {
		width: vwM(15);
		margin-right: vwM(8);
	  }

	  @media (max-width: 640px){
		margin-right: vwS(15);
		width: vwS(16);
	  }
	}

	&-description {
	  font-size: rem(14);
	  font-weight: normal;
	  font-stretch: normal;
	  font-style: normal;
	  line-height: 1.36;
	  letter-spacing: normal;
	  color: $brownish-grey;

	  @media (max-width: 1366px) {
		font-size: remM(14);
	  }

	  @media (max-width: 640px){
		display: none;
	  }
	}
  }
}

h1 {
  font-size: rem(30);
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.27;
  letter-spacing: normal;
  color: $black;
  padding-top: vw(15);
  padding-bottom: vw(8);
  margin: 0;

  @media (max-width: 1366px) {
	font-size: remM(30);
	padding-top: vwM(15);
	padding-bottom: vwM(8);
  }

  @media (max-width: 640px) {
	font-size: remS(30);
	padding-top: vwS(15);
	padding-bottom: vwS(8);
  }
}

.sub-title {
  font-size: rem(16);
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: $black;
  margin: 0;

  @media (max-width: 1366px) {
	font-size: remM(16);
  }

  @media (max-width: 640px) {
	font-size: remS(16);

	br{
	  display: none;
	}
  }


}

.present {
  width: 100%;
  padding: 0;
  margin: 0;
  margin-top: vw(30);
  line-height: 0;

  img {
	width: 100%;
  }

  @media (max-width: 1366px) {
	margin-top: vwM(30);
  }

  @media (max-width: 640px) {
	margin-top: vwS(15);
  }
}

.tabs {
  background-color: $dark-blue;

  &-item {
	width: 100%;
	border-bottom: solid 1px #405265;
	padding: vw(30) vw(45);


	@media (max-width: 1366px) {
	  padding: vwM(30) vwM(45);
	}

	@media (max-width: 640px) {
	  padding: vwS(15);
	}

	&:hover {
	  background-color: #22374b;
	}

	&__header {
	  width: 100%;
	  display: inline-flex;
	  justify-content: space-between;
	  align-items: center;
	  cursor: pointer;

	  span {
		font-size: rem(22);
		font-weight: normal;
		font-stretch: normal;
		font-style: normal;
		line-height: 1.27;
		letter-spacing: normal;
		color: $white;

		@media (max-width: 1366px) {
		  font-size: remM(22);

		}

		@media (max-width: 640px) {
			font-size: remS(22);
		  	max-width: 80%;
		}
	  }

	  img {
		width: vw(14);

		@media (max-width: 1366px) {
		  width: vwM(14);

		}

		@media (max-width: 640px) {
		  width: vwS(14);
		}
	  }

	}

	&__description {
	  width: 100%;
	  max-width: vw(693);

	  p {
		font-size: rem(16);
		font-weight: normal;
		font-stretch: normal;
		font-style: normal;
		line-height: 1.5;
		letter-spacing: normal;
		color: #ffffff;

		@media (max-width: 1366px) {
		  font-size: remM(16);
		}

		@media (max-width: 640px) {
		  font-size: remS(16);
		  padding: 0;
		  margin: 0;
		}
	  }


	  a {
		color: $light-blue;
		text-decoration: none;
		border-bottom: 1px dashed $light-blue;

		&:hover {
		  border: none;
		}
	  }

	  @media (max-width: 1366px) {
		max-width: vwM(693);
	  }

	  @media (max-width: 640px) {
		max-width: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
	  }
	}

	&__body {
	  width: 100%;
	  display: none;
	  align-items: flex-start;
	  justify-content: space-between;

	  @media (max-width: 640px) {
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
	  }

	  img.decor {
		margin-left: auto;
		width: vw(250);

		@media (max-width: 1366px) {
		  width: vwM(250);
		}

		@media (max-width: 640px) {
		  display: inline-flex;
		  width: vwS(250);
		  margin-right: auto;
		  text-decoration: none;
		  margin-top: vwS(30);
		  margin-bottom: vwS(15);

		  &.mobile-hidden{
			display: none;
		  }
		}

	  }
	}

	&__active {

	  &:hover {
		background-color: inherit;
	  }

	  .tabs-item__header {
		margin-bottom: vw(29);
		padding-bottom: vw(30);
		border-bottom: solid 1px #405265;

		img {
		  transform: rotate(180deg);
		  transform-origin: center center;
		}

		@media (max-width: 1366px) {
		  margin-bottom: vwM(29);
		  padding-bottom: vwM(30);
		}

		@media (max-width: 640px) {
		  margin-bottom: vwS(15);
		  padding-bottom: vwS(15);
		}

	  }

	  .tabs-item__body {
		display: flex;
	  }
	}
  }
}

.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: vw(280);

  @media (max-width: 1366px){
	width: vwM(280);
  }

  @media (max-width: 640px){
	width: 100%;
  }

  &-label {
	width: 100%;
	position: relative;
	margin-bottom: vw(30);

	@media (max-width: 1366px){
	  margin-bottom: vwM(30);
	}

	@media (max-width: 640px){
	  margin-bottom: vwS(30);
	}

	&:hover{
	  .form-delete{
		display: inline-flex;
	  }
	}
  }

  &-delete {
	position: absolute;
	top: auto;
	right: 0;
	bottom: vw(8);
	left: auto;
	width: vw(14);
	display: none;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	z-index: 2;

	@media (max-width: 1366px){
	  width: vwM(14);
	  bottom: vwM(8);
	}


	@media (max-width: 640px){
	  width: vwS(14);
	  bottom: vwS(8);
	}


	img{
	  width: 100%;
	}
  }

  &-submit{
	border-radius: 100px;
	border: 1px solid $white;
	font-size: rem(16);
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.88;
	letter-spacing: normal;
	text-align: center;
	background: transparent;
	padding: vw(10) vw(20);
	color: $white;

	&:hover{
	  background: $white;
	  color: $dark-blue;
	  cursor: pointer;
	}

	@media (max-width: 1366px){
	  font-size: remM(16);
	  padding: vwM(10) vwM(20);
	}

	@media (max-width: 640px){
	  width: 100%;
	  font-size: remS(16);
	  padding: vwS(10) vwS(20);
	  margin-top: vwS(15);
	  margin-bottom: vwS(15);
	}
  }

  &-input {
	width: 100%;
	color: $white;
	font-size: rem(16);
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.5;
	letter-spacing: normal;
	color: $white;
	background: transparent;
	padding-bottom: vw(3);
	outline: none;
	border: none;
	border-bottom: solid 1px #5c738b;
	position: relative;
	z-index: 1;

	@media (max-width: 1366px){
	  font-size: remM(16);
	  padding-bottom: vwM(3);
	}

	@media (max-width: 640px){
	  font-size: remS(16);
	  padding-bottom: vwS(3);
	}

	&:hover,
	&:focus{
	  border-color: $white;

	  &+.form-delete{
		display: inline-flex;
	  }
	}

	&::placeholder {
	  font-size: rem(16);
	  color: $white;

	  @media (max-width: 1366px){
		font-size: remM(16);
	  }

	  @media (max-width: 640px){
		font-size: remS(16);
	  }
	}
  }
}

.mobile-hidden{
  @media (max-width: 640px){
	display: none;
  }
}

.mobile-show{
  display: none;
  @media (max-width: 640px){
	display: inline-flex;
  }
}